import styled from 'styled-components';
import { OpenSeaPort, Network } from 'opensea-js';
import {useEffect, useState} from "react";

import Page from "../../components/Page";
import Card, { ResponsiveCard } from '../../components/Card';
import SelectCheckbox from "../../components/UI/SelectCheckbox";
import Collections from "../../components/Collections";
import {useActiveWeb3React} from "../../hooks";
import Orders from "./components/Orders";

const Row = styled.div`
  margin-top: -30px;
  margin-left: -10px;
  margin-right: -10px;
`

const Col = styled.div`
  padding: 0 10px 20px;
`

const Sidebar = styled(Col)`
  width: 340px;
  
  @media (max-width: 991px) {
    width: 100%;
  }
`

const Content = styled(Col)`
  flex: 1;
`

const StyledCard = styled(Card)`
  .card-body {
    padding: 0;
  }
`

const StyledResponsiveCard = styled(ResponsiveCard)`

  .card-body {
    padding: 0;
    
    
    @media (max-width: 991px) {
      padding: 0 10px;
    }
  }
  
  @media (max-width: 767px) {
    background-color: transparent;
    border-top: 1px solid rgba(255, 255, 255, 0.5);
  }
  
  
`

const CardSection = styled.div`
  padding: ${({ paddingTop, paddingBottom }) => `${paddingTop || '20px'} 20px ${paddingBottom || '20px'}`};
  border-bottom: ${({ hasBorder }) => hasBorder ? "1px solid rgba(255, 255, 255, 0.5)" : 'none'};
  display: flex;
  flex-direction: column;
`

const Title = styled.h4`
  font-weight: bold;
  font-size: 1.25rem;
  color: ${({ theme }) => theme.text1};
  margin: ${({ marginBottom }) => `0 0 ${marginBottom || '20px'}`};
  
  @media (max-width: 991px) {
    font-size: 1.125rem;
  }

`

let seaport;
let web3;

const NFT = props => {
    const { account, library } = useActiveWeb3React();
    const [selectedCollection, setSelectedCollection] = useState(undefined);

    const changeSelectedCollection = (collection) => {
        setSelectedCollection(collection.slug)
    }

    useEffect(() => {
        seaport = new OpenSeaPort(window.ethereum || library, {
            networkName: Network.Main
        })
        web3 = seaport.web3;
    }, [account])

    return (
        <Page title={'NFT'} morePadding size={'lg'}>
            <Row className="d-flex flex-column align-items-stretch flex-lg-row align-items-lg-start">
                <Sidebar>
                    <StyledCard>
                        <CardSection>
                            <Title>Collections</Title>
                            <Collections onChangeCollection={changeSelectedCollection}/>
                        </CardSection>
                    </StyledCard>
                </Sidebar>
                <Content>
                    <StyledResponsiveCard>
                        <CardSection>
                            <Orders
                                seaport={seaport}
                                web3={web3}
                                selectedCollection={selectedCollection}
                                />
                        </CardSection>
                    </StyledResponsiveCard>
                </Content>
            </Row>
        </Page>
    )
}

export default NFT;
