import { Col, FormControl, InputGroup } from 'react-bootstrap';
import {useState, useEffect, useCallback} from "react";
import Table from 'react-bootstrap-table-next';
import Skeleton from "react-loading-skeleton";
import styled from 'styled-components';
import { isMobile } from 'react-device-detect';

import './currencies.scss';
import SearchIcon from '../../../assets/images/search.svg';
import CustomCard from "../../../components/CustomCard";
import MarketApi from "../../../http/market";
import SparklineChart from "../../../components/SparklineChart";
import ResponsiveTable from "../../../components/ResponsiveTable";
import {Link} from "react-router-dom";

const GotoMarketContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 24px 14px;
  
  @media (min-width: 992px) {
    padding: 0 24px 10px;
    margin-top: -1rem;
  }
`

const marketApi = new MarketApi();

let typingInterval;

const Currencies = props => {
    const [coins, setCoins] = useState([{}, {}, {}, {}, {}]);
    const [query, setQuery] = useState('')

    const getSearchedCoins = useCallback(async (query) => {
        try {
            const coins = await marketApi.get('search', {
                locale: 'en',
                query: query,
            })
            if(coins.length > 0) {

                const res = await marketApi.get('searchedCoins', {
                    ids: coins.slice(0, 5).map(coin => coin.id)
                })

                if(res.hasOwnProperty('data')) {
                    setCoins(res.data.slice(0, 5));
                }
            } else {
                setCoins([])
            }
        } catch(e) {
            setCoins([{}, {}, {}, {}, {}])
        }


    }, [])

    useEffect(() => {
        if(query === '') {
            marketApi.get('all', {
                page: 1,
                pageSize: 5
            })
                .then(response => {
                    if(response.hasOwnProperty('data')) {
                        setCoins(response.data);
                    }
                })
                .catch(e => {
                    setCoins([])
                })
        } else {
            clearTimeout(typingInterval);
            typingInterval = setTimeout(() => getSearchedCoins(query), 300);

        }
    }, [query])

    const columns = [
        {
            dataField: 'name',
            text: "Name",
            formatter: (cell, row) => {
                return (
                    <div className="d-flex coin align-items-center">
                        {row.hasOwnProperty('image') ? (
                            <div className="coin__icon">
                                <img src={row?.image} alt={row?.name}/>
                            </div>
                        ) : (
                            <Skeleton width={isMobile ? 24 : 55} height={isMobile ? 24 : 55} className={isMobile ? 'mr-2' : 'mr-4'}/>
                        )}

                        <span className={'coin__symbol'}>
                            {row.hasOwnProperty('symbol') ? row?.symbol?.toUpperCase() : (
                                <Skeleton width={isMobile ? 30 : 80} />
                            )}
                        </span>
                        <span className={'coin__name'}>
                            {row.hasOwnProperty('name') ? row?.name : (
                                <Skeleton width={isMobile ? 40 : 120} />
                            )}
                        </span>
                    </div>
                )
            },
            style: {
                paddingLeft: 60
            }
        },
        {
            dataField: 'price',
            text: "Last Price",
            formatter: (cell, row) => {
                return (
                    <span className={'coin__price'}>{row.hasOwnProperty('current_price') ? `$${row?.current_price}` : (
                        <Skeleton width={isMobile ? 50 : 80} height={32}/>
                    )}
                    </span>
                )
            }
        },
        {
            dataField: 'price_change',
            text: "24h Change",
            formatter: (cell, row) => {
                const data = row?.price_change_percentage_24h?.toFixed(4);
                return row.hasOwnProperty('current_price') ? (
                    <span className={`label label-inline ${isMobile ? 'label-sm' : 'label-lg'} label-light-${data > 0 ? "success" : "danger"}`}>{data > 0 ? '+' : ''}{data ? `${data}%` : ''}</span>
                ) : (
                    <Skeleton width={80} height={32}/>
                )
            }
        },
        {
            dataField: 'sparkline',
            text: "Markets",
            formatter: (cell, row, index) => {
                const data = row?.sparkline_in_7d?.price;
                return row.hasOwnProperty('sparkline_in_7d') ? (
                    <SparklineChart data={data} theme={index % 2 === 1 ? 'secondary' : 'primary'}/>
                ) : (
                    <Skeleton width={120} height={40}/>
                )
            },
            style: {
                width: 180
            }
        }
    ]

    return (
        <section className="row currency section d-flex align-items-stretch">
            <Col xs={12} className={'d-flex flex-column flex-lg-row align-items-stretch align-items-lg-center justify-content-between section__title'}>
                <div className="d-flex align-items-start justify-content-between">
                    <h2 className="h2 mr-auto">Currencies</h2>
                    <Link to={'/markets'} className={'d-flex d-lg-none'}><button className="btn btn-link p-0" style={{ fontWeight: 500 }}>Go To Market</button></Link>
                </div>
                <InputGroup className={'w-auto'}>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            <img src={SearchIcon} alt="search"/>
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl id="inlineFormInputGroup" placeholder="Search" value={query} onChange={e => setQuery(e.target.value)} className={'form-control--currency'} />
                </InputGroup>
            </Col>
            <Col xs={12}>
                <CustomCard>
                    {coins.length > 0 ? (
                        <>
                            <Table
                                wrapperClasses="table-responsive d-none d-lg-block"
                                bordered={false}
                                classes={`table currencies table-vertical-center overflow-hidden table-dark-border table-hover`}
                                bootstrap4
                                remote
                                keyField="id"
                                columns={columns}
                                data={coins} />
                            <ResponsiveTable breakpoint={'lg'} columns={columns} data={coins}/>
                            <GotoMarketContainer>
                                <Link to={'/markets'}><button className="btn btn-link p-0" style={{ fontWeight: 500 }}>Go To Market</button></Link>
                            </GotoMarketContainer>
                        </>
                    ) : (
                        <span className="py-5 font-size-sm font-weight-medium d-flex align-items-center justify-content-center">There is no token.</span>
                    )}

                </CustomCard>
            </Col>
        </section>
    )
}

export default Currencies;