import React, { Component } from 'react';
import styled from 'styled-components';

import Loading from "../Loading";
import { emitter } from "../../lib/helper";

const Wrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10999;
  background-color: ${({theme}) => theme.bg1};
  background-image: linear-gradient(201.32deg, #4E5780 -48.82%, #283763 51.35%);
  display: flex;
  visibility: ${props => props.loaded ? 'hidden' : 'visible'};
  opacity: ${props => props.loaded ? 0 : 1};
  align-items: center;
  justify-content: center;
  transition: all ease 0.4s;
  
`

class SplashScreen extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: props.loading || false
        }
    }
    componentDidMount() {
        emitter.on('loading', this.loadHandler);
        emitter.on('stop-loading', this.loadHandler.bind(this, false));
    }

    componentWillUnmount() {
        emitter.off('loading', this.loadHandler);
        emitter.off('stop-loading', this.loadHandler.bind(this, false));
    }

    loadHandler = (value = true) => {
        this.setState({
            loading: value,
        })
    }
    render() {
        return (
            <Wrapper loaded={!this.state.loading}>
                <Loading width={'88'} height={'88'} color={'primary'} active={true}/>
            </Wrapper>
        )
    }
}

export default SplashScreen;