import styled from 'styled-components';
import Skeleton from 'react-loading-skeleton';
import {useMemo} from "react";
import Img from "../UI/Img";

const Wrapper = styled.button`
  display: flex;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  
  &:hover,
  &:active,
  &:focus {
    outline: none;
    text-decoration: none;
    box-shadow: none;
  }
`

const LogoContainer = styled.div`
  width: 32px;
  height: 32px;
  min-width: 32px;
  border-radius: 32px;
  margin-right: 20px;
`

const Logo = styled(Img)`
    width: 100%;
    height: 100%;
    border: 1px solid white;
    border-radius: 320px;
    
`

const Name = styled.span`
  color: white;
  font-size: .875rem;
  font-weight: 400;
  text-align: left;
`

const CollectionCard = props => {
    const loading = props.collection.loading;

    const skeletons = useMemo(() => {
        const result = [];
        if(loading) {
            const count = Math.floor(Math.random() * 2 + 1);
            for(let i = 0; i < count; i++) {
                const width = Math.floor(Math.random() * (count > 2 ? 50 : 70) + 30);
                result.push((
                    <Skeleton width={width} className={'mr-2'}/>
                ))
            }
        }

        return result;
    }, [loading])

    return (
        <Wrapper onClick={props.clickHandler}>
            {loading ? (
                <>
                    <LogoContainer>
                        <Skeleton width={'100%'} height={'100%'} circle/>
                    </LogoContainer>
                    {skeletons}
                </>
            ) : (
                <>
                    <LogoContainer>
                        <Logo src={props.collection.image_url} alt={props.collection.name}/>
                    </LogoContainer>
                    <Name>{props.collection.name}</Name>
                </>
            )}

        </Wrapper>
    )
}

export default CollectionCard;
