import { Card } from 'react-bootstrap';

import './style.scss';

const { Header, Body } = Card;

const CustomCard = props => {
    return (
        <Card className={'custom-card'}>
            {props.header && (
                <Header>{props.header}</Header>
            )}
            <Body>
                {props.children || props.body}
            </Body>
        </Card>
    )
}

export default CustomCard;