import { Switch, Route, Redirect } from 'react-router-dom';
import {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";

import HomePage from "./pages/Home";
import Dashboard from "./pages/Dashboard";
import Platform from "./pages/Platform";
import Pools from "./pages/Pools";
import Swap from "./pages/Swap";
import Spot from "./pages/Spot";
import InstantSwap from "./pages/InstantSwap";
import Markets from "./pages/Markets";
import Explore from "./pages/Explore";
import ExploreTypeList from "./pages/ExploreTypeList";
import CoinDetails from "./pages/CoinDetails";
import Governance from "./pages/Governance";
import CreateProposal from "./pages/CreateProposal";
import Proposals from "./pages/Proposals";
import Vote from "./pages/Vote";
import History from './pages/History';
import Wallet from './pages/Wallet';
import FiatOff from "./pages/FiatOff";
import FiatOn from "./pages/FiatOn";
import {useActiveWeb3React} from "./hooks";
import {useMemoTokenBalances} from "./state/balances/hooks";
import {fetchBalances, fetchTransformedBalances} from "./state/balances/actions";
import SplashScreen from "./components/SplashScreen";
import {fetchCurrencies} from "./state/currency/actions";
import NFT from "./pages/NFT";
import Borrow from "./pages/Borrow";

const Routes = props => {
    const { account } = useActiveWeb3React();

    const selectedCurrency = useSelector(state => state.currency.selected)
    const balances = useSelector((state) => state.balances.data);
    const { ETH } = useSelector((state) => state.currency.currenciesRate)
    const dispatch = useDispatch();
    const walletBalances = useMemoTokenBalances()


    useEffect(() => {
        dispatch(fetchCurrencies(selectedCurrency));
    }, [selectedCurrency, dispatch]);


    useEffect(() => {
        if(account) {
            dispatch(fetchBalances(account))
        }
    }, [account, dispatch])

    useEffect(() => {
        dispatch(fetchTransformedBalances(balances, walletBalances, ETH));
    }, [balances, walletBalances, ETH, dispatch]);

    return (
        <>
            <SplashScreen />
            <Switch>
                <Route
                    path={'/'}
                    component={HomePage}
                    exact
                />
                {account && (
                    <>
                        <Route
                            path={'/dashboard'}
                            component={Dashboard}
                        />
                        <Route
                            path={'/platforms/:platform'}
                            component={Platform}
                        />
                        <Route
                            path={'/markets'}
                            component={Markets}
                        />
                        <Route
                            path={'/fiat/off'}
                            component={FiatOff}
                        />
                        <Route
                            path={'/fiat/on'}
                            component={FiatOn}
                        />
                        <Route
                            path={'/invest/pools'}
                            component={Pools}
                        />
                        <Route
                            path={'/invest/nft'}
                            component={NFT}
                        />
                        <Route
                            path={'/invest/borrow'}
                            component={Borrow}
                        />
                        <Route
                            path={'/trading/spot'}
                            component={Spot}
                        />
                        <Route
                            path={'/trading/swap'}
                            component={Swap}
                        />
                        <Route
                            path={'/trading/instant-swap'}
                            component={InstantSwap}
                        />
                        <Route path={'/tools/governance'} exact component={Governance}/>
                        <Route path={'/tools/governance/:space/create'} exact component={CreateProposal}/>
                        <Route path={'/tools/governance/:space'} exact component={Proposals}/>
                        <Route path={'/tools/governance/:space/proposal/:id'} exact component={Vote}/>
                        <Route path={'/tools/explore'} exact component={Explore}/>
                        <Route path={'/tools/explore/:type'} exact component={ExploreTypeList}/>
                        <Route path={'/tools/market/:id'} exact component={CoinDetails}/>
                        <Route path={'/coins/:id'} exact component={CoinDetails}/>
                        <Route path={'/account/history'} exact component={History}/>
                        <Route path={'/account/wallet'} exact component={Wallet}/>
                    </>
                )}
                <Redirect from={'*'} to={'/'}/>
            </Switch>


        </>
    )
}

export default Routes;
