import styled from 'styled-components';
import {FormControl, InputGroup} from "react-bootstrap";
import React, {useEffect, useState, useMemo, useCallback} from "react";

import SearchIcon from "../../assets/images/search.svg";
import List from "./List";
import OpenSeaApi from "../../http/opensea";
import {useActiveWeb3React} from "../../hooks";


const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
`

const CustomInputGroup = styled(InputGroup)`
  margin-bottom: 20px;
  
  .input-group-text {
    padding-left: 1.25rem;
  }
`

const api = new OpenSeaApi();

const Collections = props => {
    const { account } = useActiveWeb3React();
    const [loading, setLoading] = useState(true)
    const [collections, setCollections] = useState([])
    const [query, setQuery] = useState('');

    const filteredCollections = useMemo(() => {
        if(query === '') {
            return collections;
        } else {
            const lowerQuery = query.toLowerCase();
            return collections.filter(c => JSON.stringify({n: c.name, d: c.description}).includes(lowerQuery));
        }
    }, [collections, query])

    const changeQueryHandler = useCallback((e) => {
        setQuery(e.target.value);
    }, [])

    useEffect(() => {
        setLoading(true);
        api.get('collections', {
            params: {
                limit: 300
            }
        })
            .then(response => {
                setLoading(false);
                if(response.data.hasOwnProperty('collections')) {
                    setCollections(response.data.collections);
                }
            })
            .catch(e => {
                setLoading(false);
            })
    }, [account])

    return (
        <Wrapper>
            <CustomInputGroup className={'w-auto'}>
                <InputGroup.Prepend>
                    <InputGroup.Text>
                        <img src={SearchIcon} alt="search"/>
                    </InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl id="PoolsSearch" placeholder="Search" onChange={changeQueryHandler}/>
            </CustomInputGroup>

            <List collections={filteredCollections} loading={loading} clickHandler={props.onChangeCollection}/>
        </Wrapper>
    )
}

export default Collections;
