import { Component } from 'react';
import { Row, Col } from 'react-bootstrap'

import Page from "../../components/Page";
import Card from '../../components/Card';
import MarketOverview from '../../components/MarketOverview';
import BinanceSocket from "../../http/binance";
import marketTokens from '../../constants/marketTokens.json';
import MarketCard from "./MarketCard";

class Markets extends Component {
    state = {
        data: {},
    }

    constructor(props) {
        super(props);

        this.tokens = marketTokens.tokens.filter(token => token.tags.includes('erc20')).map(token => token.symbol);
    }

    componentDidMount() {
        this.ws = new BinanceSocket(this.onOpen, this.onClose, this.onMessage, this.onError)

    }

    componentWillUnmount() {
        this.ws.close();
    }

    onOpen = (e) => {
        this.ws.send(JSON.stringify({
            "method": "SUBSCRIBE",
            "params": [
                "!ticker@arr@3000ms",
            ],
            "id": 1
        }));
    }

    onClose = (e) => {

    }

    onMessage = (e) => {
        const message = JSON.parse(e.data);

        this.transformAndSaveData(message.data);
    }

    transformAndSaveData = (data) => {
        if(!data) {
            return false;
        }

        this.setState(prevState => {
            let finalData = [];
            const regex = t => new RegExp('^' + t + '|' + t + '$', 'g')
            for(let i in data) {
                const row = data[i];

                const filteredTokens = this.tokens.filter(t => {
                    return regex(t).test(row.s);
                });
                if(filteredTokens.length > 0) {
                    const index = row.s.indexOf(filteredTokens[0]);
                    const pair = [row.s.slice(0, index === 0 ? filteredTokens[0].length : index), row.s.slice(index === 0 ? filteredTokens[0].length : index)];
                    finalData[row.s] = {
                        ...row,
                        pair
                    };
                }
            }

            return {
                data: {
                    ...prevState.data,
                    ...finalData,
                }
            }
        })
    }

    onError = (e) => {
        console.log(`[error] ${e}`)
    }

    render() {
        const data = Object.values(this.state.data);

        return (
            <Page title={'Markets'} morePadding>
                <Row style={{ marginTop: -20 }} className={'custom-row d-flex align-items-stretch'}>
                    {[...Array(4)].map((item, index) => {
                        const row = data[index] || {};

                        const percent = Number(row.p);
                        return (
                            <Col xs={12} md={6} lg={3} className={'d-flex flex-column align-items-stretch'}>
                                <Card className={'h-100'}>
                                    <MarketOverview
                                        row={row.hasOwnProperty('pair')}
                                        pair={row.pair}
                                        price={row.c}
                                        variant={percent > 0 ? 'success' : 'danger'}
                                        changePercent={`${percent > 0 ? '+' : ''}${percent?.toFixed(2)}%`}
                                        volume={!!row && `${row.q} ${row.pair?.[0]}`}
                                    />
                                </Card>
                            </Col>
                        )
                    })}
                </Row>
                <Row>
                    <Col>
                        <MarketCard markets={this.state.data} />
                    </Col>
                </Row>
            </Page>
        )
    }
}

export default Markets;